<template>
  <div class="box">
    <br>
    <i class="iconfont  logo">Hellow World</i>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  methods: {
    show() {

    }
  },
  mounted() {
    this.show()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #000;
}

.iconfont.logo {
  position: relative;
  color: #fff;
  font-size: 5em;
  text-shadow: 0 0 50px #0f0,
    0 0 100px #0f0,
    0 0 150px #0f0,
    0 0 200px #0f0,
    0 0 250px #0f0;
  animation: animate 5s linear infinite;
}

@keyframes animate {
  0% {
    filter: hue-rotate(0deg);
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

.iconfont {
  font-family: Colonna MT;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-apple:before {
  content: "\e7dc";
}
</style>



