<template>
  <div class="page">
    <br>
    <h2>{{ msg }}</h2>
    <br>
    <table class="gridtable" style="margin:auto">
      <tr>
        <th>学科</th>
        <th>分数</th>
      </tr>
      <tr>
        <td>语文</td>
        <td><input type="text" name="" id="" v-model.number="chinese" /></td>
      </tr>
      <tr>
        <td>数学</td>
        <td><input type="text" name="" id="" v-model.number="math" /></td>
      </tr>
      <tr>
        <td>英语</td>
        <td><input type="text" name="" id="" v-model.number="english" /></td>
      </tr>
    </table>
    <br>
    <Button type="warning" @click="scoreJS">计算成绩</Button>
    <br>
    <br>
    <div style="background:#eee;padding: 20px;width: 220px;margin: auto;">
      <Card :bordered="true">
        <p slot="title">计算结果</p>
        <p>总成绩:&nbsp{{ sum }}&nbsp|&nbsp平均分:&nbsp{{ average }}</p>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: '分数计算',
      chinese: 0,
      math: 0,
      english: 0,
      sum: 0,
      average: 0,
    }
  },
  methods:
  {
    scoreJS: function () {
      this.sum = this.chinese + this.math + this.english;
      this.average = Math.round((this.sum / 3) * 10.0) / 10.0;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.page {
  margin: auto;
}

table.gridtable {
  font-family: verdana, arial, sans-serif;
  font-size: 11px;
  color: #333333;
  border-width: 1px;
  border-color: #666666;
  border-collapse: collapse;
}

table.gridtable th {
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #666666;
  background-color: #dedede;
}

table.gridtable td {
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #666666;
  background-color: #ffffff;
}
</style>



