import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Scores from '@/components/Scores'
import Xxmlt from '@/components/Xxmlt'
import Mmxhs from '@/components/Mmxhs'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/scores',
      name: 'Scores',
      component: Scores
    },
    {
      path: '/xxmlt',
      name: 'Xxmlt',
      component: Xxmlt
    },
    {
      path: '/mmxhs',
      name: 'Mmxhs',
      component: Mmxhs
    }
  ]
})



