<template>
  <div class="page">
    <br>
    <h2>{{ msg }}</h2>
    <br>
    <table class="gridtable" style="margin:auto">
      <tr>
        <td colspan="2"><img width="310" src=".\assets\mlt.jpg"></td>
      </tr>
      <tr>
        <th>类型</th>
        <th>数量</th>
      </tr>
      <tr>
        <td>荤菜 1.5 元/两</td>
        <td><input type="text" name="" id="" v-model.number="meet" />两</td>
      </tr>
      <tr>
        <td>素菜 1 元/两</td>
        <td><input type="text" name="" id="" v-model.number="vage" />两</td>
      </tr>
      <tr>
        <td colspan="2"><Button type="warning" @click="moneyJS">计算总价</Button></td>
      </tr>
      <tr>
        <td colspan="2">
          <Card :bordered="true">
            <p slot="title">计算结果</p>
            <p>总价：{{ Math.round(sum * 100.0) / 100.0 }}&nbsp元</p>
          </Card>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: '西西麻辣烫',
      sum: 0,
      meet: 0,
      vage: 0,
    }
  },
  methods:
  {
    moneyJS: function () {
      this.sum = this.meet * 1.5 + this.vage * 1;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.page {
  margin: auto;
}

.timer {
  color: red
}

.page {
  margin: auto;
}

table.gridtable {
  font-family: verdana, arial, sans-serif;
  font-size: 11px;
  color: #000000;
  border-width: 1px;
  border-color: #ff5a5a;
  border-collapse: collapse;
}

table.gridtable th {
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #ffbcbc;
  background-color: #ffbebe;
}

table.gridtable td {
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #faa3a3;
  background-color: #ffffff;
}
</style>



